
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Loading from 'vux/src/components/loading/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import {mapState} from 'vuex'

export default {
name: 'index',
directives: {
TransferDom
},
data() {
return {
tabbars: [
{
title: '贷款主页',
default_icon: 'icon-zhuye',
active_icon: 'icon-zhuye',
link: {
name: 'Index',
params: this.$route.params,
query: this.$route.query,
replace: true
}
},
{
title: '联系客服',
default_icon: 'icon-tubiao313',
active_icon: 'icon-tubiao313',
link: {
name: 'ContactUs',
params: this.$route.params,
query: this.$route.query,
replace: true
}
}
]
}
},
components: {
Loading,
ViewBox,
Tabbar,
TabbarItem
},
methods: {},
computed: {
...mapState({
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction,
isLeader: state => state.vux.userInfo
})
},
async mounted() {
console.log(this.$route)
}
}



